* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  src: url("./fonts/ArchivoNarrow-Regular.ttf");
  font-family: "Archivo";
  font-weight: normal;
}

body {
  font-family: "Archivo";
  font-weight: normal;
  background-color: #1a143b;
  color: #ffffff;
}

a {
  color: #ffffff;
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  width: 1280px;
  margin: 0 auto;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100vh;
}